.img-mask {
  -webkit-mask: url(data:image/svg+xml;base64,)
    100% -55vh / cover no-repeat;
}
.img-mask {
  position: absolute;
  right: 0px;
  top: 0px;
  height: 100%;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  z-index: 1;
}

.underline {
  cursor: pointer;
  border-bottom: 2px dotted rgb(150, 155, 163);
  color: rgb(222, 228, 234);
  margin-right: 8px;
  font-size: 15px;
  text-decoration: none;
}
.underline:hover {
  cursor: help;
  color: rgb(64, 142, 221);
}

.open-source-collective {
  cursor: pointer;
  color: rgb(222, 228, 234);
  text-decoration: none;
  font-size: 15px;
}
.open-source-collective:hover {
  color: rgb(255, 255, 255);
}
</style>
